/**
 * This component has been marked for deprecation
 */

import React from "react";
import { useMediaQuery } from "react-responsive";
import { MediaQuery } from "../styles/spec";
import {
    ContentContainer,
    ContentDescription,
    ContentTitle,
    Link,
    ListItemContainer,
    StyledIcon,
} from "./list.styles";
import { ListItemProps } from "./types";
import { generateExternalLink } from "../../util/urlConverter";

export const ListItem = ({ listItem, onItemClick }: ListItemProps) => {
    const isMobileOrTablet = useMediaQuery({
        query: MediaQuery.MaxWidth.tablet,
    });

    const { title = "", description = "", url = "#", internalLink } = listItem;

    const renderContent = () => (
        <>
            <ContentContainer>
                <ContentTitle
                    data-testid="list-item-title"
                    weight="semibold"
                    hasDescription={!!description}
                >
                    {title}
                </ContentTitle>
                <ContentDescription data-testid="list-item-description">
                    {description}
                </ContentDescription>
            </ContentContainer>
            <StyledIcon type="chevron-right" />
        </>
    );

    // =============================================================================
    // EVENT HANDLERS
    // =============================================================================

    const handleListItemClick = (event: React.MouseEvent) => {
        if (internalLink) {
            event.preventDefault();
            if (onItemClick) {
                onItemClick(listItem);
            }
        }
    };

    // =============================================================================
    // RENDER
    // =============================================================================

    return (
        <ListItemContainer>
            <Link
                onClick={handleListItemClick}
                href={generateExternalLink(url, isMobileOrTablet)}
                target="_blank"
                rel="noopener noreferrer"
            >
                {renderContent()}
            </Link>
        </ListItemContainer>
    );
};
