import debounce from "lodash/debounce";
import React, { useEffect, useRef, useState } from "react";
import { useMediaQuery } from "react-responsive";
import { Modal } from "../modal/modal";
import { MediaWidths } from "../styles/spec/media";
import { Text } from "../text/text";
import { Bubble, MobileModalBox, OffsetPosition } from "./popover.styles";
import { PopoverProps } from "./types";

export const Popover = ({
    children,
    visible,
    onMobileClose,
    ...otherProps
}: PopoverProps) => {
    // =============================================================================
    // CONST, STATE, REF
    // =============================================================================
    const testId = otherProps["data-testid"] || "popover";
    const [offset, setOffset] = useState<OffsetPosition>(undefined);
    const bubbleRef = useRef<HTMLDivElement>(null);
    const isMobile = useMediaQuery({
        maxWidth: MediaWidths.mobileL,
    });

    // =============================================================================
    // EFFECTS
    // =============================================================================
    useEffect(() => {
        recalculatePosition();
        window.addEventListener("resize", debounce(handleViewportResize, 300));

        return () => {
            window.removeEventListener(
                "resize",
                debounce(handleViewportResize, 300)
            );
        };
    }, []);

    // =============================================================================
    // EVENT HANDLERS
    // =============================================================================
    const handleViewportResize = () => {
        recalculatePosition();
    };

    const handleMobileClose = () => {
        if (onMobileClose) {
            onMobileClose();
        }
    };

    // =============================================================================
    // HELPER FUNCTIONS
    // =============================================================================
    const recalculatePosition = () => {
        if (bubbleRef.current) {
            const bubbleRect = bubbleRef.current.getBoundingClientRect();
            const paddingBuffer = 24;

            // Exceed top
            if (bubbleRect.y < paddingBuffer) {
                if (bubbleRect.x < paddingBuffer) {
                    setOffset("top-left");
                } else if (
                    bubbleRect.x + bubbleRect.width >
                    window.innerWidth - paddingBuffer
                ) {
                    setOffset("top-right");
                } else {
                    // Back to original
                    setOffset("top-center");
                }
            } else {
                if (bubbleRect.x < paddingBuffer) {
                    setOffset("left");
                } else if (
                    bubbleRect.x + bubbleRect.width >
                    window.innerWidth - paddingBuffer
                ) {
                    setOffset("right");
                } else {
                    // Back to original
                    setOffset(undefined);
                }
            }
        }
    };

    // =============================================================================
    // RENDER FUNCTIONS
    // =============================================================================
    const renderContent = () => {
        if (typeof children === "string") {
            return <Text.BodySmall>{children}</Text.BodySmall>;
        }

        return children;
    };

    return (
        <>
            <Bubble
                ref={bubbleRef}
                data-testid={testId}
                visible={visible}
                offset={offset}
                {...otherProps}
            >
                {renderContent()}
            </Bubble>
            {isMobile && (
                <Modal show={visible} onOverlayClick={handleMobileClose}>
                    <MobileModalBox onClose={handleMobileClose}>
                        {renderContent()}
                    </MobileModalBox>
                </Modal>
            )}
        </>
    );
};
