import React, { useEffect, useState } from "react";
import { Checkmark, Container, Input } from "./radio-button.styles";
import { RadioButtonProps } from "./types";


export const RadioButton = ({
	className,
	checked,
	disabled,
	onClick,
	onKeyPress,
	...otherProps
}: RadioButtonProps) => {
	const [selected, setSelected] = useState<boolean>(checked);

	// =============================================================================
	// EFFECTS
	// =============================================================================
	useEffect(() => {
		setSelected(checked);
	}, [checked]);

	// =============================================================================
	// EVENT HANDLERS
	// =============================================================================
	const handleOnCheck = (
		event: React.MouseEvent<HTMLInputElement, MouseEvent> | React.KeyboardEvent<HTMLDivElement>
	) => {
		if (!disabled && !selected) {
			setSelected(!selected);
			if (onClick) {
				onClick(event as React.MouseEvent<HTMLInputElement, MouseEvent>);
			}

			if (onKeyPress) {
				onKeyPress(event as React.KeyboardEvent<HTMLInputElement>);
			}
		}
	}

	// =============================================================================
	// RENDER FUNCTION
	// =============================================================================
	return (
		<Container
			selected={selected}
			disabled={disabled}
			className={className}
			data-testid="radio-button"
			onKeyPress={handleOnCheck}
			tabIndex={disabled ? -1 : 0}
			role="radio"
		>
			<Input
				type="radio"
				data-testid="radio-input"
				onClick={handleOnCheck}
				disabled={disabled}
				tabIndex={-1} // Need this else it is able to be tabbed to
				{...otherProps}
			/>
			<Checkmark
				id="checkmark"
				data-testid="checkmark"
				disabled={disabled}
				selected={selected}
			/>
		</Container>
	);
};
