import React from "react";
import { LayoutProps } from "./types";

type Ref = HTMLDivElement;
export const Container = React.forwardRef<Ref, LayoutProps>((props, ref) => {
	const { className, ...otherProps } = props;
	const libClassName = "sgds-container";
	const mergedClassName = className ? `${libClassName} ${className}` : libClassName;

	return (
		<div {...otherProps} className={mergedClassName} ref={ref}>
			{props.children}
		</div>
	);
});
