import React from "react";
import styled from "styled-components";
import { LayoutProps } from "./types";

type Ref = HTMLDivElement;
export const Section = React.forwardRef<Ref, LayoutProps>((props, ref) => {
	const { className, children, ...otherProps } = props;
	const libClassName = "sgds-section";
	const mergedClassName = props.className ? `${libClassName} ${props.className}` : libClassName;

	return (
		<Component {...otherProps} className={mergedClassName} ref={ref}>
			{children}
		</Component>
	);
});

// =============================================================================
// STYLING
// =============================================================================
const Component = styled.section`
	padding: 0 1.5rem;
	// Extra enforcement
	padding-left: 1.5rem !important;
	padding-right: 1.5rem !important;
`;
