import React, { SyntheticEvent, useEffect, useRef, useState } from "react";
import ReactDOM from "react-dom";
import { OverlayProps } from "./types";
import { Root, Wrapper } from "./overlay.styles";

export const Overlay = ({
    show = false,
    root,
    onOverlayClick,
    children,
    backgroundOpacity = 0.8,
    backgroundBlur = true,
    disableTransition = false,
    enableOverlayClick = false,
}: OverlayProps) => {
    // =============================================================================
    // CONST, STATE, REF
    // =============================================================================
    const [lastScrolledPosition, setLastScrolledPosition] = useState<number>(0);
    const [documentBody, setDocumentBody] = useState<HTMLElement>(null);

    const childRef = useRef<HTMLDivElement>(null);
    const childWithRef =
        children && React.cloneElement(children, { ref: childRef });

    // =============================================================================
    // EFFECTS
    // =============================================================================
    useEffect(() => {
        if (document.body) {
            setDocumentBody(document.body);
        }

        if (show) {
            const currentScrollPos = window.scrollY;
            setLastScrolledPosition(currentScrollPos);
            setTimeout(() => {
                document.body.classList.add("overlay-open");
                document.body.style.top = `-${currentScrollPos}px`; // retain position in the background
            }, 200); // 200ms based on the overlay animation
        } else {
            document.body.classList.remove("overlay-open");
            document.body.style.top = "";
            document.body.style;
            window.scrollTo(0, lastScrolledPosition);
        }
    }, [show]);

    useEffect(() => {
        addStylesheet();

        return () => {
            if (document.body.classList.contains("overlay-open")) {
                document.body.classList.remove("overlay-open");
                document.body.style.top = "";
            }

            removeStylesheet();
        };
    }, []);

    // =============================================================================
    // HELPER FUNCTIONS
    // =============================================================================
    const addStylesheet = () => {
        /**
         * This stylesheet is to manipulate the <body>
         * to prevent scrolling when the overlay is visible
         */
        if (!document.getElementById("overlay-stylesheet")) {
            const overlayStyleSheet = document.createElement("style");
            overlayStyleSheet.id = "overlay-stylesheet";
            overlayStyleSheet.innerHTML = `
				.overlay-open {
					position: fixed;
					left: 0;
					width: 100%;
					overflow: hidden;
					-ms-overflow-style: none;
					scrollbar-width: none;
				}

				.overlay-open::-webkit-scrollbar {
					display: none;
				}
			`;
            document.body.appendChild(overlayStyleSheet);
        }
    };

    const removeStylesheet = () => {
        const overlayStyleSheet = document.getElementById("overlay-stylesheet");
        if (overlayStyleSheet) {
            const sheetParent = overlayStyleSheet.parentNode;
            sheetParent.removeChild(overlayStyleSheet);
        }
    };

    // =============================================================================
    // EVENT HANDLERS
    // =============================================================================
    const onClickWrapper = (event: SyntheticEvent) => {
        event.preventDefault();

        const modal = childRef.current?.firstChild;
        if (modal && (modal as any).contains(event.target)) {
            return;
        } else if (onOverlayClick && enableOverlayClick) {
            onOverlayClick();
        }
    };

    // =============================================================================
    // RENDER
    // =============================================================================
    const renderWrapper = () => (
        <Wrapper
            data-testid={"overlay-wrapper"}
            show={show}
            backgroundOpacity={backgroundOpacity}
            backgroundBlur={backgroundBlur}
            disableTransition={disableTransition}
            enableOverlayClick={enableOverlayClick}
            onClick={onClickWrapper}
        >
            {childWithRef}
        </Wrapper>
    );

    const renderComponent = () => (
        <Root id="overlay-root" data-testid="overlay-root" show={show}>
            {children && renderWrapper()}
        </Root>
    );

    return documentBody
        ? ReactDOM.createPortal(renderComponent(), documentBody)
        : null;
};
