/**
 * This component has been marked for deprecation
 */

import React from "react";
import { Button } from "../button";
import { ListItem } from "./list-item";
import {
    ButtonIcon,
    ViewBar,
    ListContainer,
    ListTitle,
    Wrapper,
} from "./list.styles";
import { ListProps, ListItemType } from "./types";

export const List = ({
    listItems,
    title,
    onItemClick,
    maxItemsShown,
}: ListProps) => {
    const [isCollapsed, setIsCollapsed] = React.useState(true);

    const handleListItemClick = (item: ListItemType) => {
        if (onItemClick) {
            onItemClick(item);
        }
    };

    const toggleSection = () => {
        setIsCollapsed(!isCollapsed);
    };

    const itemsToDisplay = isCollapsed
        ? listItems.slice(0, maxItemsShown)
        : listItems;
    const showCollapsible = maxItemsShown < listItems.length;

    return (
        <Wrapper>
            {title && <ListTitle data-testid={"list-title"}>{title}</ListTitle>}
            <ListContainer>
                {itemsToDisplay.map((listItem, i) => (
                    <ListItem
                        key={`${listItem.title}-${i}`}
                        listItem={listItem}
                        onItemClick={handleListItemClick}
                    />
                ))}
            </ListContainer>
            {showCollapsible && (
                <ViewBar>
                    <Button.Default styleType="light" onClick={toggleSection}>
                        {isCollapsed ? "View more" : "View less"}
                        {isCollapsed ? (
                            <ButtonIcon type="plus" />
                        ) : (
                            <ButtonIcon type="minus" />
                        )}
                    </Button.Default>
                </ViewBar>
            )}
        </Wrapper>
    );
};
