import { ThemeMap } from "./types";

/**
 * These functions are adapted from
 * https://github.com/styled-components/styled-theming
 */

const getThemeValue = (
	attrKey: string,
	props: any,
	values: ThemeMap,
	defaultMapKey: string
) => {
	const mapKey = props.theme && props.theme[attrKey];
	const themeValue = values[mapKey] || values[defaultMapKey];

	return themeValue;
};

/**
 * Retrieves the style value based on the theme
 * @param name the attribute in the theme
 * @param values the style map
 * @param defaultMapKey the default fallback key for the style map
 */

export const theme = (name: string, values: ThemeMap, defaultMapKey: string) => {
	return (props: any) => {
		return getThemeValue(name, props, values, defaultMapKey);
	}
}
