import React from "react";
import { BannerProps } from "./types";
import {
    Container,
    HeadingFocusText,
    ForegroundOverlay,
    HeadingText,
    Wrapper,
} from "./banner.styles";

export const Banner = ({ children, imgset, ...otherProps }: BannerProps) => {
    const renderText = () => {
        if (!imgset) {
            return <HeadingFocusText>{children}</HeadingFocusText>;
        } else {
            return (
                <HeadingText
                    foreground={!!imgset?.foreground}
                    data-testid="with-image"
                >
                    {children}
                </HeadingText>
            );
        }
    };

    return (
        <Wrapper
            imgset={imgset}
            aria-label={otherProps["aria-label"]}
            role="banner"
        >
            {imgset && imgset.foreground && (
                <ForegroundOverlay foreground={imgset.foreground} />
            )}
            <Container imgset={imgset}>{renderText()}</Container>
        </Wrapper>
    );
};
