import React, { useEffect, useState } from "react";
import { Container, Input, Label } from "./toggle-button.styles";
import { ToggleButtonProps } from "./types";

export const ToggleButton = ({
    disabled,
    checked,
    onClick,
    onKeyPress,
    type = "checkbox",
    children,
    className,
    ...otherProps
}: ToggleButtonProps) => {
    const [selected, setSelected] = useState<boolean>(checked);

    // =============================================================================
    // EFFECTS
    // =============================================================================
    useEffect(() => {
        setSelected(checked);
    }, [checked]);

    // =============================================================================
    // EVENT HANDLERS
    // =============================================================================
    const handleOnClick = (
        event:
            | React.MouseEvent<HTMLInputElement, MouseEvent>
            | React.KeyboardEvent<HTMLDivElement>
    ) => {
        if (!disabled) {
            if ((type === "radio" && !selected) || type === "checkbox") {
                setSelected(!selected);
                if (onClick) {
                    onClick(
                        event as React.MouseEvent<HTMLInputElement, MouseEvent>
                    );
                }

                if (onKeyPress) {
                    onKeyPress(event as React.KeyboardEvent<HTMLInputElement>);
                }
            }
        }
    };

    // =============================================================================
    // RENDER FUNCTIONS
    // =============================================================================
    return (
        <Container
            onKeyPress={handleOnClick}
            selected={selected}
            disabled={disabled}
            tabIndex={disabled ? -1 : 0}
            role="button"
            aria-pressed={selected}
            className={className}
        >
            <Input
                type="checkbox"
                data-testid="toggle-button-input"
                disabled={disabled}
                onClick={handleOnClick}
                tabIndex={-1} // Need this else it is able to be tabbed to
                {...otherProps}
            />
            <Label
                weight={selected ? "bold" : "regular"}
                selected={selected}
                disabled={disabled}
                data-testid="toggle-button-label"
            >
                {children}
            </Label>
        </Container>
    );
};
