import React from "react";
import { ButtonProps, ButtonRef, MainStyleProps } from "./types";
import { Main } from "./button.style";

/**
 * NOTE: Due to the way we intend to customise both components, with forwardRef behaviour
 * we are unable to create a single component and have them share.
 *
 * Will refactor if there is a better way
 */
export const Default = React.forwardRef<ButtonRef, ButtonProps>(
    (props, ref) => {
        const {
            children,
            disabled = false,
            styleType = "default",
            ...otherProps
        } = props;

        const mainStyle: MainStyleProps = {
            buttonStyle: disabled ? "disabled" : styleType,
            buttonSize: "default",
        };

        return (
            <Main
                ref={ref}
                data-testid={otherProps["data-testid"] || "button"}
                disabled={disabled}
                {...mainStyle}
                {...otherProps}
            >
                <span>{children}</span>
            </Main>
        );
    }
);

export const Small = React.forwardRef<ButtonRef, ButtonProps>((props, ref) => {
    const {
        children,
        disabled = false,
        styleType = "default",
        ...otherProps
    } = props;

    const mainStyle: MainStyleProps = {
        buttonStyle: disabled ? "disabled" : styleType,
        buttonSize: "small",
    };

    return (
        <Main
            ref={ref}
            data-testid={otherProps["data-testid"] || "button"}
            disabled={disabled}
            {...mainStyle}
            {...otherProps}
        >
            <span>{children}</span>
        </Main>
    );
});

export const Button = {
    Default,
    Small,
};
