import styled, { keyframes } from "styled-components";
import { Color } from "../styles/theme";
import { RadioButtonProps } from "./types";

// =============================================================================
// STYLE INTERFACE
// =============================================================================
interface StyleProps {
    selected?: boolean;
    disabled?: boolean;
}

// =============================================================================
// STYLING
// =============================================================================
export const Container = styled.div<StyleProps>`
    display: flex;
    justify-content: center;
    align-items: center;
    height: 1.5rem;
    width: 1.5rem;
    position: relative;
    border-radius: 50%;
    border: 2px solid ${Color.Accent.Light[2]};

    ${(props) => {
        if (props.disabled) {
            return `
				border: 2px solid ${Color.Neutral[4](props)};
			`;
        }
    }}
`;

export const Input = styled.input<RadioButtonProps>`
    position: absolute;
    opacity: 0;
    height: 100%;
    width: 100%;
    cursor: ${(props) => (props.disabled ? "not-allowed" : "pointer")};
    z-index: 1;
`;

export const Checkmark = styled.div<StyleProps>`
    height: 0.75rem;
    width: 0.75rem;
    opacity: ${(props) => (props.selected ? 1 : 0)};
    transition: all 200ms ease-in-out;
    background: ${(props) =>
        props.disabled ? Color.Neutral[4](props) : Color.Primary(props)};
    border-radius: 50%;
    border: 1px solid transparent;
`;
