// Generated with util/create-component.js
import React, { useEffect, useRef, useState } from "react";
import { MediaWidths } from "../styles/spec";
import {
    Caret,
    Content,
    CurrentLabel,
    Fade,
    Item,
    PreviousLink,
    Wrapper,
} from "./breadcrumb.style";
import { BreadcrumbProps } from "./types";

export const Breadcrumb = ({
    links,
    fadeColor,
    fadePosition = "both",
    itemStyle,
    id,
    ...otherProps
}: BreadcrumbProps) => {
    const [showFade, setShowFade] = useState<boolean>(!!fadePosition);
    const [showFadeLeft, setShowFadeLeft] = useState<boolean>(
        fadePosition === "left" || fadePosition === "both"
    );
    const [showFadeRight, setShowFadeRight] = useState<boolean>(
        fadePosition === "right" || fadePosition === "both"
    );
    const wrapperRef = useRef<HTMLDivElement>(null);
    const contentRef = useRef<HTMLUListElement>(null);

    // =============================================================================
    // HANDLERS
    // =============================================================================
    const handleBreadcrumbScroll = () => {
        if (showFade) {
            // Set fade if the media is smaller than or equal to tablet
            setShowFade(window.innerWidth < MediaWidths.tablet);

            const content = contentRef.current;
            const wrapper = wrapperRef.current;
            if (content && wrapper) {
                if (content.scrollWidth > wrapper.offsetWidth) {
                    setShowFade(true);
                    setShowFadeLeft(content.scrollLeft >= 1);
                    setShowFadeRight(
                        content.scrollWidth - content.scrollLeft >
                            wrapper.offsetWidth
                    );
                } else {
                    setShowFade(false);
                }
            } else {
                setShowFade(false);
            }
        }
    };

    const handleLinkClick = (event: React.MouseEvent, linkFn: () => void) => {
        if (linkFn) {
            event.preventDefault();
            linkFn();
        } else {
            return;
        }
    };

    // =============================================================================
    // EFFECTS
    // =============================================================================
    useEffect(() => {
        handleBreadcrumbScroll();
        window.addEventListener("resize", handleBreadcrumbScroll);
        const content = contentRef.current;
        if (content) {
            content.addEventListener("scroll", handleBreadcrumbScroll);
        }
        // cleanup
        return () => {
            window.removeEventListener("resize", handleBreadcrumbScroll);
            if (content) {
                content.removeEventListener("scroll", handleBreadcrumbScroll);
            }
        };
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(() => {
        const content = contentRef.current;
        if (content && window.innerWidth <= MediaWidths.tablet) {
            content.scrollLeft = 800;
        }
    }, [links]);

    // =========================================================================
    // RENDER
    // =========================================================================
    if (!links) return null;

    const renderLinks = () => {
        return links.map((link, index) => {
            let element: JSX.Element;
            if (index === links.length - 1 || !link.url) {
                element = (
                    <CurrentLabel weight="semibold">{link.title}</CurrentLabel>
                );
            } else {
                element = (
                    <PreviousLink
                        href={link.url}
                        weight="semibold"
                        onClick={(e) => handleLinkClick(e, link.onClick)}
                    >
                        {link.title}
                    </PreviousLink>
                );
            }

            return (
                <Item key={index} styleProps={itemStyle}>
                    {element}
                    {index < links.length - 1 && <Caret type="chevron-right" />}
                </Item>
            );
        });
    };

    const renderFade = () => {
        return (
            <>
                {showFadeLeft && (
                    <Fade backgroundColor={fadeColor} position={"left"} />
                )}
                {showFadeRight && (
                    <Fade backgroundColor={fadeColor} position={"right"} />
                )}
            </>
        );
    };

    return (
        <Wrapper ref={wrapperRef} id={id || "breadcrumb"} {...otherProps}>
            <nav>
                <Content ref={contentRef}>{renderLinks()}</Content>
            </nav>
            {showFade && renderFade()}
        </Wrapper>
    );
};
