import React, { useEffect, useRef, useState } from "react";
import { useSpring } from "react-spring";
import {
    Container,
    Label,
    List,
    ListItem,
    ListItemSelector,
    SearchInput,
    SearchInputContainer,
} from "./dropdown-list.styles";
import { DropdownListProps } from "./types";

/**
 * NOTE: This component is not directly exportables
 * but forms part of a component
 */
export const DropdownList = <T,>({
    listItems,
    listExtractor,
    valueExtractor,
    onSelectItem,
    listStyleWidth,
    visible,
    enableSearch,
    onSearch,
    searchFunction,
    ...otherProps
}: DropdownListProps<T>) => {
    // =============================================================================
    // CONST, REF, STATE
    // =============================================================================
    const [focusedListIndex, setFocusedIndex] = useState<number>(0);
    const [searchValue, setSearchValue] = useState<string>("");
    const [displayListItems, setDisplayListItems] = useState<T[]>(listItems);
    const [listHeight, setListHeight] = useState<number>(0);

    const nodeRef = useRef<HTMLDivElement>();
    const listRef = useRef<HTMLUListElement>();
    const listItemRefs = [];
    for (let i = 0; i < listItems.length; i++) {
        listItemRefs.push(useRef());
    }
    const searchInputRef = useRef<HTMLInputElement>();

    // =============================================================================
    // EFFECTS
    // =============================================================================
    useEffect(() => {
        document.addEventListener("keydown", handleKeyboardPress);
        return () => {
            document.removeEventListener("keydown", handleKeyboardPress);
        };
    }, [focusedListIndex]);

    useEffect(() => {
        filterAndUpdateList(searchValue);
    }, [searchValue]);

    useEffect(() => {
        setSearchValue("");

        if (visible) {
            const listHeight = getListHeight();
            setListHeight(listHeight);

            if (searchInputRef && searchInputRef.current) {
                searchInputRef.current.focus();
            } else if (
                listItemRefs[0] &&
                listItemRefs[focusedListIndex].current
            ) {
                listItemRefs[focusedListIndex].current.focus();
            }
        } else {
            setListHeight(0);
        }
    }, [visible, focusedListIndex]);

    useEffect(() => {
        if (visible) {
            // safeguard
            const listHeight = getListHeight();
            setListHeight(listHeight);
        }
    }, [displayListItems]);

    // =============================================================================
    // HELPER FUNCTIONS
    // =============================================================================
    const formatValue = (value: any) => {
        return valueExtractor ? valueExtractor(value) : value;
    };

    const formatKeyValue = (value: any, index: number) => {
        const formattedValue = valueExtractor ? valueExtractor(value) : value;
        // This is needed as some items might have the same value
        return `item_${index}__${formattedValue}`;
    };

    const formatListItemLabel = (value: any): string => {
        return listExtractor ? listExtractor(value) : value.toString();
    };

    const filterAndUpdateList = (searchValue: string) => {
        if (searchValue === "") {
            // reset
            setDisplayListItems(listItems);
        } else if (searchFunction) {
            const updated = searchFunction(searchValue);
            setDisplayListItems(updated);
        } else {
            const updated = listItems.filter((item) => {
                const displayValue = formatListItemLabel(item).toLowerCase();
                return displayValue.includes(searchValue.trim().toLowerCase());
            });
            setDisplayListItems(updated);
        }
    };

    const getListHeight = () => {
        return listRef && listRef.current
            ? listRef.current.getBoundingClientRect().height
            : 0;
    };

    // =============================================================================
    // EVENT HANDLERS
    // =============================================================================
    const handleKeyboardPress = (event: KeyboardEvent) => {
        if (nodeRef && (nodeRef.current as any).contains(event.target)) {
            // inside click
            switch (event.code) {
                case "ArrowDown":
                    if (focusedListIndex < listItems.length - 1) {
                        // last element cannot go further
                        const upcomingIndex = focusedListIndex + 1;
                        if (listItemRefs[upcomingIndex]) {
                            listItemRefs[upcomingIndex].current.focus();
                        }
                        setFocusedIndex(upcomingIndex);
                    }
                    break;
                case "ArrowUp":
                    if (focusedListIndex > 0) {
                        const upcomingIndex = focusedListIndex - 1;
                        if (listItemRefs[upcomingIndex]) {
                            listItemRefs[upcomingIndex].current.focus();
                        }
                        setFocusedIndex(focusedListIndex - 1);
                    }
                    break;
                default:
                    break;
            }
        }
        return;
    };

    const handleListItemClick = (event: React.MouseEvent, item: any) => {
        event.preventDefault();
        if (onSelectItem) {
            onSelectItem(item, formatValue(item));
        }
    };

    const handleSearchInputChange = (
        event: React.ChangeEvent<HTMLInputElement>
    ) => {
        const value = event.target.value;
        setSearchValue(value);
    };

    // =============================================================================
    // RENDER FUNCTIONS
    // =============================================================================

    const renderItems = () => {
        return displayListItems.map((item, index) => {
            return (
                <ListItem key={formatKeyValue(item, index)}>
                    <ListItemSelector
                        onClick={(event) => handleListItemClick(event, item)}
                        ref={listItemRefs[index]}
                        data-testid={`list-item`}
                        type="button"
                        tabIndex={visible ? 0 : -1}
                    >
                        <Label>{formatListItemLabel(item)}</Label>
                    </ListItemSelector>
                </ListItem>
            );
        });
    };

    const renderSearchInput = () => {
        if (enableSearch || searchFunction) {
            return (
                <SearchInputContainer key="search">
                    <SearchInput
                        ref={searchInputRef}
                        onChange={handleSearchInputChange}
                        value={searchValue}
                        data-testid="search-input"
                        aria-label="search-input"
                    />
                </SearchInputContainer>
            );
        }

        return null;
    };

    // React spring animation configuration
    const styles = useSpring({
        height: listHeight,
    });

    return (
        <Container
            style={styles}
            data-testid="dropdown-container"
            ref={nodeRef}
        >
            <List
                ref={listRef}
                data-testid="dropdown-list"
                width={listStyleWidth}
                role="list"
                {...otherProps}
            >
                {renderSearchInput()}
                {renderItems()}
            </List>
        </Container>
    );
};
